.header__link {
    /*margin-top: 6px;*/
    text-decoration: none;
    color: white;
    text-align: right;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
  }
  
  .header__link:hover {
    opacity: 0.8;
  }
  
  @media screen and (max-width: 580px) {
    .header__link {
      font-size: 14px;
      margin-right: 30px;
    }
  }